button,
a.action.primary,
a.action.back {
	overflow: visible;
	width: auto;
	border: 0;
	padding: 0;
	margin: 0;
	background: transparent;
	cursor: pointer;
	color: #fff;
	vertical-align: middle;
	line-height: 40px;
}

button span,
a.action.primary span,
a.action.back span {
	display: block;
	width: 100%;
	padding: 0 15px;
	white-space: nowrap;
	transition: all 0.8s ease 0s;
	text-align: center;
	text-transform: normal;
	color: #fff;
	font-size: 15px;
	line-height: inherit;
	letter-spacing: -0.1px;
	border: 0 solid #000;
	background: #000;
}

button.disabled,
button[disabled],
a.action.primary.disabled,
a.action.primary[disabled],
a.action.back.disabled,
a.action.back[disabled] {
	cursor: default;
	pointer-events: none;
}

button.disabled span,
button[disabled] span,
a.action.primary.disabled span,
a.action.primary[disabled] span,
a.action.back.disabled span,
a.action.back[disabled] span {
	background: #000;
	border-color: #000;
	color: #fff;
}

button:hover span,
a.action.primary:hover span,
a.action.back:hover span {
	border-color: #fff;
	color: #fff;
	background: #999;
	transition: all 0.8s ease 0s;
}

button.small,
a.action.primary.small,
a.action.back.small {
	max-width: 180px;
