	width: 100%;
}

button.medium,
a.action.primary.medium,
a.action.back.medium {
	max-width: 230px;
	font-size: 15px;
	width: 100%;
}

button.large,
a.action.primary.large,
a.action.back.large {
	max-width: 275px;
