/* ============================================ *
 * Primary button
 * ============================================ */

.primary button {
	color: #fff;
	background: #000;
	height: 40px;
	line-height: 40px;
	transition: all 0.8s ease 0s;
}

.primary button:hover {
	color: #fff;
	background: #999;
	transition: all 0.8s ease 0s;
}

.primary button span {
	color: #fff;
}

.primary button span:hover {
	color: #fff;
	background: #999;
}

.primary button span.disabled,
.primary button span[disabled] {
	cursor: default;
	pointer-events: none;
}

.primary button span.active {
	color: #fff;
	background: #999;
}

/* ============================================ *
 * Secondary button
 * ============================================ */

.secondary button {
	color: #000;
	background: #fff;
}

.secondary button span {
	color: #000;
	font-size: 10px;
	background: #fff;
}

.secondary button span:hover {
	background: transparent;
	color: #000;
}

.secondary a {
	color: #000;
	font-size: 10px;
}

/* ============================================ *
 * Default
 * ============================================ */

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button { /* display: none; <- Crashes Chrome on hover */
	-webkit-appearance: none;
	margin: 0; /* <-- Apparently some margin are still there even though it's hidden */
}

input[name=email],
input[type="text"],
input[type="number"],
input.input-text,
select,
textarea {
	display: block;
	width: 100%;
	max-width: 100%;
	background: #fff;
	border: 1px solid #9b9b9b;
	color: #000;
}

input[name=email]:focus,
input[type="text"]:focus,
input[type="number"]:focus,
input.input-text:focus,
select:focus,
textarea:focus {
	outline: 0;
	outline: none;
	border-color: #000;
}

textarea {
	padding: 15px;
	overflow: auto;
	width: 100%;
	min-width: 280px;
	min-height: 120px;
}

input.radio {
	margin-right: 3px;
}

input.qty,
input.item-qty {
	width: 42px;
	max-width: 42px;
	text-align: center;
	padding: 0 5px;
}

.quantity-wrap {
	display: flex;
	align-items: center;
	justify-content: flex-start;
}

.quantity-wrap .minus,
.quantity-wrap .plus {
	width: 30px;
	height: 30px;
	display: block;
	background: #9b9b9b;
	color: #fff;
	position: relative;
	font-size: 0;
}

.quantity-wrap .minus:before,
.quantity-wrap .plus:before {
	font-family: 'bolt-icons';
	font-style: normal;
	font-weight: normal;
	font-variant: normal;
	text-transform: none;
	line-height: 1; /* Better Font Rendering =========== */
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	content: "";
	position: absolute;
	left: 0;
	right: 0;
	top: 0;
	bottom: 0;
	margin: auto;
	font-size: 16px;
	line-height: 16px;
	text-align: center;
	display: block;
	width: 16px;
	height: 16px;
}

.quantity-wrap .plus:before {
	font-family: 'bolt-icons';
	font-style: normal;
	font-weight: normal;
	font-variant: normal;
	text-transform: none;
	line-height: 1; /* Better Font Rendering =========== */
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	content: "";
}

.quantity-wrap span {
	padding: 0;
}

input[type=number] {
	-moz-appearance: textfield; /* Firefox */
}

input {
	margin: 0;
	line-height: 40px;
	height: 42px;
	padding: 0 15px;
	outline: 0;
	outline: none;
	padding-right: 40px;
}

input[type="file"] {
	padding: 0;
}

input[type="checkbox"] {
	height: inherit;
	display: inline-block;
	margin: 2px 5px 0 0;
	position: relative;
	top: 2px;
	width: auto;
}

select {
	display: block;
	margin: 0;
	padding: 5px 15px 5px 5px;
	border: 1px solid #9b9b9b;
	font-size: 12px !important;
	font-family: sans-serif;
	color: #444;
	line-height: 1.3;
	outline: 0;
	-moz-appearance: none;
	-webkit-appearance: none;
	appearance: none;
	cursor: pointer;
	background-color: #fff;
	background-image: url("data:image/svg+xml;charset=US-ASCII,%3Csvg%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%20width%3D%22292.4%22%20height%3D%22292.4%22%3E%3Cpath%20fill%3D%22%23007CB2%22%20d%3D%22M287%2069.4a17.6%2017.6%200%200%200-13-5.4H18.4c-5%200-9.3%201.8-12.9%205.4A17.6%2017.6%200%200%200%200%2082.2c0%205%201.8%209.3%205.4%2012.9l128%20127.9c3.6%203.6%207.8%205.4%2012.8%205.4s9.2-1.8%2012.8-5.4L287%2095c3.5-3.5%205.4-7.8%205.4-12.8%200-5-1.9-9.2-5.5-12.8z%22%2F%3E%3C%2Fsvg%3E");
	background-repeat: no-repeat, repeat;
	background-position: right 5px top 50%,0 0;
	background-size: 8px auto,100%;
	height: 42px;
	width: 100%;
}

select::-ms-expand {
	display: none;
}

select:hover {
	border-color: #888;
}

select:focus {
	border-color: #000;
	color: #000;
	outline: none;
}

select::-moz-focus-inner {
	border: 0;
}

select option {
	font-weight: normal;
}

select.multiselect option {
	border-bottom: 1px solid #9b9b9b;
	padding: 2px 5px;
}

select.multiselect option:last-child {
	border-bottom: 0;
}

fieldset,
.fieldset {
	border: 0;
	margin: 0;
	padding: 0;
}

fieldset .field,
.fieldset .field {
	border: 0;
	padding: 0;
	margin-bottom: 15px;
}

fieldset .field label,
.fieldset .field label {
	width: 100%;
	display: block;
	margin-bottom: 5px;
}

fieldset .field.note,
.fieldset .field.note {
	display: block;
	margin-bottom: 30px;
}

fieldset .field.newsletter,
.fieldset .field.newsletter {
	line-height: 1;
}

fieldset .field.choice label,
.fieldset .field.choice label {
	margin: 0;
	width: auto;
	display: inline-block;
}

fieldset .legend,
.fieldset .legend {
	margin: 0 0 15px;
	padding: 0 0 15px;
	width: 100%;
	display: block !important;
	border-bottom: 1px solid #9b9b9b;
	font-size: 16px;
	font-weight: bold;
}

.fieldset span.note {
	display: block;
	padding-top: 5px;
	font-size: 10px;
}

.fieldset ._with-tooltip {
	position: relative;
	padding-right: 30px;
}

.field-tooltip {
	cursor: pointer;
	position: absolute;
	right: 0px;
	top: 10px;
}

.field-tooltip:before {
	font-family: 'bolt-icons';
	font-style: normal;
	font-weight: normal;
	font-variant: normal;
	text-transform: none;
	line-height: 1; /* Better Font Rendering =========== */
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	content: "";
	width: 20px;
	height: 20px;
	font-size: 20px;
	color: #9b9b9b;
}

.field-tooltip span {
	display: none;
}

.field-tooltip .field-tooltip-content {
	background: #fff;
	border: 1px solid #9b9b9b;
	border-radius: 1px;
	font-size: 14px;
	padding: 15px;
	width: 270px;
	display: none;
	left: 25px;
	position: absolute;
	text-transform: none;
	top: -16px;
	word-wrap: break-word;
	z-index: 2;
	font-size: 10px;
}

.field-tooltip._active .field-tooltip-content {
	display: block;
}

.field-tooltip:hover .field-tooltip-content {
	display: block;
}

.field.required > .label:after {
	content: '*';
	margin: 0 0 0 5px;
}

/* ============================================ *
 * Password Strength Meter
 * ============================================ */

.password-strength-meter {
	background-color: #f4f4f4;
	height: 32px;
	line-height: 32px;
	padding: 0 9px;
	position: relative;
	z-index: 1;
}

.password-strength-meter:before {
	content: '';
	height: 100%;
	left: 0;
	position: absolute;
	top: 0;
	z-index: -1;
}

.password-none .password-strength-meter:before {
	background-color: #f4f4f4;
	width: 100%;
}

.password-weak .password-strength-meter:before {
	background-color: #ffafae;
	width: 25%;
}

.password-medium .password-strength-meter:before {
	background-color: #ffd6b3;
	width: 50%;
}

.password-strong .password-strength-meter:before {
	background-color: #c5eeac;
	width: 75%;
}

.password-very-strong .password-strength-meter:before {
	background-color: #81b562;
	width: 100%;
}

/* ============================================ *
 * Tables
 * ============================================ */

table {
	border: 0 none;
	empty-cells: show;
	font-size: 100%;
	width: 100%;
	border-collapse: collapse;
}

table > caption {
	border: 0;
	clip: rect(0 0 0 0);
	height: 1px;
	margin: -1px;
	overflow: hidden;
	padding: 0;
	position: absolute;
	width: 1px;
}

table th {
	padding: 10px;
	white-space: nowrap;
	font-size: 14px;
	font-weight: bold;
	border-bottom: 1px solid #9b9b9b;
}

table tr:nth-child(even) {
	background-color: #eee;
}

table td {
	padding: 10px;
	border-bottom: 1px solid #9b9b9b;
}

caption,
th,
td {
	vertical-align: top;
	text-align: left;
	font-weight: normal;
}

/* Data Table */

.data.table thead { /* background-color:#f2f2f2; */
}

.data.table tr {
	float: none;
}

.data.table tbody.odd td {
	border-width: 0 1px;
}

.data.table tbody.even {
	background-color: #f6f6f6;
}

.data.table tbody.even td {
	border-width: 0 1px;
}

.data.table tbody.odd tr.border td,
.data.table tbody.even tr.border td {
	border-bottom-width: 1px;
}

.data.table th .tax-flag {
	white-space: nowrap;
	font-weight: normal;
}

.data.table td.label,
.data.table th.label {
	font-weight: bold;
	background-color: #f6f6f6;
}

