/* ============================================ *
 * Header
 * ============================================ */

.page-header {
	background: #fff;
	position: relative;
	padding: 0;
	margin-bottom: 30px;
}

.before-header {
	clear: both;
}

.before-header::after {
	display: block;
	clear: both;
	content: "";
}

.after-header {
	clear: both;
}

.after-header::after {
	display: block;
	clear: both;
	content: "";
}

.header-top-message {
	background: #000;
	color: #9b9b9b;
	text-align: center;
	width: 100%;
	display: inline-block;
	padding: 15px;
	font-size: 14px;
}

.header {
	display: flex;
	flex-direction: column;
	padding: 0;
}

.header .header-top {
	padding-top: 30px;
	padding-bottom: 30px;
	width: 100%;
	padding-right: 15px;
	padding-left: 15px;
	margin-right: auto;
	margin-left: auto;
}

.header .header-top .nav-toggle {
	display: none;
	position: relative;
	width: 100%;
	padding-right: 15px;
	padding-left: 15px;
	flex: 0 0 16.6666666667%;
	max-width: 16.6666666667%;
}

.header .skip.contentarea {
	border: 0 none;
	clip: rect(0px, 0px, 0px, 0px);
	height: 1px;
	margin: -1px;
	overflow: hidden;
	padding: 0;
	position: absolute;
	width: 1px;
}

.header .logo {
	text-align: center;
	display: flex;
	align-items: center;
	justify-content: center;
	position: relative;
	width: 100%;
	padding-right: 15px;
	padding-left: 15px;
	flex: 0 0 58.3333333333%;
	max-width: 58.3333333333%;
}

.header .logo .h1,
.header .logo h1 {
	display: flex;
	align-items: center;
}

.header .logo img {
	max-width: 150px;
}

.header-row {
	display: flex;
	flex-wrap: wrap;
	margin-right: -15px;
	margin-left: -15px;
}

.nav-sections {
	position: relative;
	width: 100%;
	padding-right: 15px;
	padding-left: 15px;
}

.header-controls {
	text-align: right;
	position: relative;
	width: 100%;
	padding-right: 15px;
	padding-left: 15px;
	flex: 0 0 25%;
	max-width: 25%;
}

.header-controls > ul {
	display: flex;
	flex-wrap: wrap;
	margin-right: -15px;
	margin-left: -15px;
}

.header-controls li.control-link {
	position: relative;
	width: 100%;
	padding-right: 15px;
	padding-left: 15px;
	flex: 0 0 33.3333333333%;
	max-width: 33.3333333333%;
	padding: 0;
	text-align: center;
	-webkit-touch-callout: none;
	-webkit-user-select: none;
	-khtml-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;
}

.header-controls li.control-link .controls {
	cursor: pointer;
	font-size: 14px;
	display: flex;
	line-height: 1;
}

.header-controls li.control-link span.icon {
	font-size: 16px;
	margin-right: 5px;
}

/* ============================================ *
 * Header Search
 * ============================================ */

.header-search {
	background: #ccc;
	overflow: hidden;
	max-height: 0;
	transition: all 0.5s;
}

.header-search::after {
	display: block;
	clear: both;
	content: "";
}

.header-search.active {
	max-height: 72px;
}

.header-search .block-content {
	width: 100%;
	padding-right: 15px;
	padding-left: 15px;
	margin-right: auto;
	margin-left: auto;
	max-width: 960px;
	padding: 15px 0;
}

.header-search .minisearch {
	display: flex;
	flex-wrap: wrap;
	margin-right: -15px;
	margin-left: -15px;
}

.header-search .field.search {
	position: relative;
	width: 100%;
	padding-right: 15px;
	padding-left: 15px;
	flex: 0 0 75%;
	max-width: 75%;
	padding-right: 0;
}

.header-search .field.search .control {
	position: relative;
}

.header-search label {
	border: 0;
	clip: rect(0 0 0 0);
	height: 1px;
	margin: -1px;
	overflow: hidden;
	padding: 0;
	position: absolute;
	width: 1px;
}

.header-search input {
	border: 0 none;
	width: 100%;
	padding-left: 30px;
}

.header-search .actions {
	position: relative;
	width: 100%;
	padding-right: 15px;
	padding-left: 15px;
	flex: 0 0 25%;
	max-width: 25%;
	padding-left: 0;
}

.header-search .actions button {
	background: none;
	border: 0 none;
	cursor: pointer;
	width: 100%;
}

.header-search .actions button span {
	background: #000;
	border: 1px solid #fff;
	padding: 0;
}

.header-search .actions button span:hover {
	background: #000;
}

.header-search .search-icon {
	width: 15px;
	height: 15px;
	position: absolute;
	top: 0;
	bottom: 0;
	left: 10px;
	margin: auto;
}

.header-search .search-close {
	display: none;
}

/* ============================================ *
 * Header Cart
 * ============================================ */

.minicart-wrapper .action.showcart {
	display: flex;
	line-height: 1;
}

.minicart-wrapper .counter {
	padding-top: 3px;
	padding-left: 5px;
}

.minicart-wrapper li.product-item {
	max-width: 100%;
}

.minicart-wrapper .ui-dialog {
	display: none;
	position: fixed;
	top: 0;
	right: 0;
	bottom: 0;
	z-index: 910;
	background-color: #fff;
	width: 25%;
	max-width: 470px;
	box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.25);
	-webkit-transition: all 0.2s ease-in-out;
	-moz-transition: all 0.2s ease-in-out;
	-o-transition: all 0.2s ease-in-out;
	transition: all 0.2s ease-in-out;
}

.minicart-wrapper .ui-dialog.active {
	display: flex;
}

.noscroll {
	position: fixed;
	overflow-y: scroll;
	width: 100%;
	left: 0;
	top: 0;
}

#header-cart-over {
	position: fixed;
	z-index: 900;
	top: 0;
	left: 0;
	display: block;
	visibility: hidden;
	width: 100%;
	height: 100%;
	-webkit-transition: all 0.5s cubic-bezier(0.645, 0.045, 0.355, 1);
	-moz-transition: all 0.5s cubic-bezier(0.645, 0.045, 0.355, 1);
	transition: all 0.5s cubic-bezier(0.645, 0.045, 0.355, 1);
	opacity: 0;
	background: #000;
}

#header-cart-over.active {
	visibility: visible;
	-webkit-transition: all 0.5s cubic-bezier(0.645, 0.045, 0.355, 1);
	-moz-transition: all 0.5s cubic-bezier(0.645, 0.045, 0.355, 1);
	transition: all 0.5s cubic-bezier(0.645, 0.045, 0.355, 1);
	opacity: 0.2;
}

/* ============================================ *
 * Header Account Links
 * ============================================ */

#header-links {
	display: none;
	position: absolute;
	top: 100%;
	right: 20px;
	background: #000;
	z-index: 995;
	text-align: left;
}

ul.header.links {
	display: none;
	position: absolute;
	top: 30px;
	left: -65px;
	width: 150px;
	background: #fff;
	z-index: 9999;
	text-align: left;
	font-size: 10px;
	border: 1px solid #ccc;
}

ul.header.links:after,
ul.header.links:before {
	bottom: 100%;
	left: 50%;
	border: solid transparent;
	content: " ";
	height: 0;
	width: 0;
	position: absolute;
	pointer-events: none;
}

ul.header.links:after {
	border-color: rgba(255, 255, 255, 0);
	border-bottom-color: #ffffff;
	border-width: 10px;
	margin-left: -10px;
}

ul.header.links:before {
	border-color: rgba(0, 0, 0, 0);
	border-bottom-color: #ccc;
	border-width: 11px;
	margin-left: -11px;
}

ul.header.links li {
	width: 100%;
}

ul.header.links li a {
	display: block;
	padding: 5px 10px;
}

ul.header.links li a:hover {
	background: #eee;
}

.link-account.active ul.header.links {
	display: block;
}

