	padding-left: 5px;
	font-weight: bold;
}

.swatch-attribute-options {
	display: flex;
	margin-top: 15px;
	flex-wrap: wrap;
}

.swatch-option {
	text-align: center;
	line-height: 35px;
	width: 35px;
	height: 35px;
	margin-right: 5px;
	margin-bottom: 5px;
	cursor: pointer;
	border: 1px solid #9b9b9b;
	text-align: center;
	position: relative;
	overflow: hidden;
	text-overflow: ellipsis;
}

.swatch-option.selected {
	background-color: #9b9b9b;
	border-color: #000;
}

.swatch-option:not(.disabled):hover {
	border-color: #000;
	color: #000;
}

.swatch-option.disabled {
	cursor: default;
}

.swatch-option.disabled:after {
	content: "";
	position: absolute;
	left: 50%;
	top: calc(-25% - 2px);
	width: 3px;
	height: calc(150% + 4px);
	transform: rotate(-45deg);
	transform-origin: 100% 50%;
	background-color: #9b9b9b;
}

.swatch-option-loading {
	content: url("../images/loader-2.gif");
}

.swatch-option-tooltip {
	border: 0;
	clip: rect(0 0 0 0);
	height: 1px;
	margin: -1px;
	overflow: hidden;
	padding: 0;
	position: absolute;
	width: 1px;
}

